* {
  box-sizing: border-box;
}

body {
  background-color: black; 
  font-family: sans-serif;
  padding: 0;

}

h1 {
  margin-top: 0;
  font-size: 24px;
}

h2 {
  margin-top: 0;
  font-size: 22px;
}

h3 {
  margin-top: 0;
  font-size: 18px;
}

h4 {
  margin-top: 0;
  font-size: 16px;
}

h5 {
  margin-top: 0;
  font-size: 14px;
}

h6 {
  margin-top: 0;
  font-size: 12px;
}

code {
  font-size: 1.2em;
}

ul {
  padding-inline-start: 20px;
}

* {
  box-sizing: border-box;
}

.square {
  background: white;
  border-color: #fff;
  border: 5px solid #999;
  float: left;
  font-size: 130px;
  font-weight: bold;
  line-height: 80px;
  height: 180px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 250px;
}

.status{
  font-size: 60px;
  font-weight: bold;
  color: #fff;
  line-height: 20px;
  height: 40px;
  margin-right: -1px;
  margin-top: -1px;
  text-align: left;
  width: 750px;
}

.board-row:after {
  color:#999;
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}
.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.game-info {
  margin-left: 20px;
}
